*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    height: 100vh;
    font-family: 'Arial';
    background-color: rgb(24,24,24);
}

body{
    overflow-x: hidden;
}

.webgl
{
    position: absolute;
    top: 0;
    left: 0;
    outline: none;
    mix-blend-mode:exclusion;
}

.container{
    height: 100vh;
    display: grid;
    place-items: center;
}

section{
    height: 100vh;
}

h1{
    font-size: 8rem;
    text-transform: capitalize;
    color:white;
}